.video-player {
  position: relative;
  font-size: 0;
  background: #000;
  z-index: 0;
  border: 1px solid #fdd20a;
}

.video-player:before {
  content: "";
  position: absolute;
  left: 20px;
  top: 20px;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.2;
  z-index: -2;
  transition: 0.5s;
}

.video-player:after {
  content: "";
  position: absolute;
  left: 0px;
  top: 12px;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.2;
  z-index: -1;
  transition: 0.5s;
}

.video-player__thumb {
  position: relative;
}

.video-player__thumb img {
  width: 100%;
}

.video-player__thumb:before {
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: #000;
  opacity: 0.1;
  position: absolute;
  transition: 0.3s;
  z-index: 1;
}

.video-player:hover .video-player__thumb:before {
  opacity: 0.3;
}

.video-player__thumb:after {
  content: "\f04b";
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  left: 50%;
  margin-left: -30px;
  margin-top: -38px;
  top: 50%;
  transform: scale(1);
  color: #fdd20a;
  font-size: 77px;
  text-shadow: 0px 7px 45px rgba(0, 0, 0, 0.7);
  transition: 0.3s;
  z-index: 2;
}

@media (max-width: 767px) {
  .video-player__thumb:after {
    font-size: 60px;
    margin-left: -30px;
    margin-top: -30px;
  }
}

.video-player:hover .video-player__thumb:after {
  transform: scale(1.4);
}

.video-player--no-shadow {
  border: 0;
}

.video-player--no-shadow:after, .video-player--no-shadow:before {
  display: none;
}

.video-player--no-shadow .video-player__thumb:before {
  display: none;
}

